.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#threadid {
	display:none;
}

#newthreadcontainer {
	display:relative;
	text-align:center;
	font-size:2rem;
}

#newthreadheader {
	display:block;
	margin-top:1rem;
	margin-bottom:1rem;
}

#newthreadtitle {
	display:block;
	margin: 0 auto;
	margin-bottom:1rem;
	width:80%;
	max-width:20rem;
}

#newthreadtext {
	display:block;
	margin: 0 auto;
	margin-bottom:1rem;
	width:80%;
	max-width:20rem;
}

#newthreadsubmit {
	display:block;
	margin: 0 auto;
	font-size:1.5rem;
}
